/* Base container styles */
.ts-apartmentDetailsContainer {
    padding: 2rem;
    max-width: 1400px;
    margin: 0 auto;
    background-color: #f5f7fa;
    min-height: 100vh;
}

/* Title section */
.ts-apartmentTitle {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
    padding: 1.5rem;
    background: white;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    font-size: 1.75rem;
    color: #2d3748;
}

.greenCheckmark {
    color: #10b981;
    font-size: 1.75rem;
    transition: transform 0.2s ease;
}

.greenCheckmark:hover {
    transform: scale(1.1);
}

.redCheckmark {
    color: #ef4444;
    font-size: 1.75rem;
    transition: transform 0.2s ease;
}

.redCheckmark:hover {
    transform: scale(1.1);
}

/* Column layout */
.ts-columns {
    display: grid;
    grid-template-columns: 1.2fr 1fr;
    gap: 2rem;
    align-items: start;
}

/* Card styles */
.ts-leftColumn, .ts-rightColumn {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.ts-leftColumn:hover, .ts-rightColumn:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
}

/* Details grid */
.ts-detailsGrid {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 1.5rem;
    background-color: #f0f9ff;
    border-radius: 12px;
    border: 1px solid #bae6fd;
}

.ts-detailItem {
    margin-top: -1rem;
}

.ts-detailItem p {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    border-bottom: 1px solid #bae6fd;
    color: #4b5563;
    line-height: 1.6;
    font-size: 1rem;
}

.ts-detailItem p:last-child {
    border-bottom: none;
}

.ts-detailItem p strong {
    color: #0369a1;
    font-weight: 600;
    margin-right: 0.5rem;
}

/* Section headers */
.ts-planningHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    width: 100%;
}

.ts-planningHeader h3,
.ts-detailsGrid .ts-planningHeader h3,
.ts-planningDetails .ts-planningHeader h3,
.ts-appointmentDetails .ts-planningHeader h3 {
    margin: 0;
    color: #2d3748;
    font-size: 1.25rem;
}

/* Planning details and Appointment details sections */
.ts-planningDetails, .ts-appointmentDetails {
    margin-top: 1.5rem;
    padding: 1.5rem;
    background-color: #f0f9ff;
    border-radius: 12px;
    border: 1px solid #bae6fd;
}

.ts-planningDetails h4, .ts-appointmentDetails h4 {
    color: #0369a1;
    margin-bottom: 1rem;
    font-size: 1.25rem;
    font-weight: 600;
}

/* Status section */
.ts-statusSection {
    border-top: 2px solid #bae6fd;
    padding-top: 1.5rem;
    margin-top: 1.5rem;
}

.ts-statusSection h4 {
    margin-bottom: 1.25rem;
    color: #059669;
    font-size: 1.25rem;
    font-weight: 600;
}

.ts-statusGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
}

.ts-statusItem {
    padding: 1rem;
    background-color: #f1f5f9;
    border-radius: 8px;
    text-align: center;
    font-size: 0.95rem;
    transition: all 0.3s ease;
    border: 1px solid #e2e8f0;
    color: #64748b;
}

.ts-statusItem.completed {
    background-color: #10b981;
    color: white;
    border: none;
    box-shadow: 0 2px 4px rgba(16, 185, 129, 0.2);
}

/* Form styles */
.ts-formGroup {
    margin-bottom: 1.5rem;
}

.ts-formGroup label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: #1e293b;
}

.ts-formGroup input[type="text"],
.ts-formGroup input[type="number"],
.ts-formGroup input[type="date"],
.ts-formGroup input[type="time"],
.ts-formGroup select,
.ts-formGroup textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
    background-color: #f8fafc;
}

.ts-formGroup input:focus,
.ts-formGroup select:focus,
.ts-formGroup textarea:focus {
    outline: none;
    border-color: #60a5fa;
    box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.2);
}

.ts-formGroup textarea {
    min-height: 120px;
    resize: vertical;
}

.ts-formGroup input[type="checkbox"] {
    width: 1.25rem;
    height: 1.25rem;
    margin-right: 0.75rem;
    accent-color: #3b82f6;
}

/* Button styles */
.ts-saveButton {
    background-color: #3b82f6;
    color: white;
    padding: 0.875rem 1.5rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.2s ease;
    width: 100%;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.ts-saveButton:hover {
    background-color: #2563eb;
    transform: translateY(-1px);
}

.ts-saveButton:active {
    transform: translateY(0);
}

.ts-saveButton:disabled {
    background-color: #94a3b8;
    cursor: not-allowed;
}

/* Appointment info styles */
.ts-appointmentDetails .ts-appointmentInfo {
    margin-top: 0;
    padding: 0;
    background-color: transparent;
    border: none;
}

.ts-appointmentDetails .ts-appointmentInfo p {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    border-bottom: 1px solid #bae6fd;
    color: #4b5563;
}

.ts-appointmentDetails .ts-appointmentInfo p:last-child {
    border-bottom: none;
}

.ts-appointmentDetails .ts-appointmentInfo p strong {
    color: #0369a1;
    font-weight: 600;
    margin-right: 0.5rem;
}

/* Timestamps section */
.ts-timestamps {
    margin-top: 1.5rem;
    padding: 1rem;
    background-color: #f0f9ff;
    border-radius: 8px;
    border: 1px solid #bae6fd;
}

.ts-timestamps p {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    border-bottom: 1px solid #bae6fd;
    color: #4b5563;
}

.ts-timestamps p:last-child {
    border-bottom: none;
}

.ts-timestamps p strong {
    color: #0369a1;
    font-weight: 600;
    margin-right: 0.5rem;
}

/* Icon styles */
.ts-icon {
    font-size: 18px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
}

.ts-editButton {
    background: none;
    border: none;
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    min-width: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s ease;
    margin-left: auto;
}

.ts-editButton:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.ts-editButton .ts-icon {
    margin: 0;
    color: #64748b;
}

.ts-saveButton .ts-icon {
    font-size: 16px;
    margin: 0;
}

/* Planning info (non-editable state) */
.ts-planningInfo p {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    border-bottom: 1px solid #bae6fd;
    color: #4b5563;
}

.ts-planningInfo p:last-child {
    border-bottom: none;
}

.ts-planningInfo p strong {
    color: #0369a1;
    font-weight: 600;
    margin-right: 0.5rem;
}

/* Appointment actions styles */
.ts-appointmentActions {
    display: flex;
    align-items: center;
    gap: 10px;
}

.ts-unifiedSchedulerButton {
    display: inline-flex;
    align-items: center;
    gap: 5px;
    padding: 8px 16px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    text-decoration: none;
    border-radius: 6px;
    font-size: 0.9rem;
    font-weight: 500;
    transition: all 0.2s ease;
    border: none;
    cursor: pointer;
    box-shadow: 0 2px 4px rgba(102, 126, 234, 0.3);
}

.ts-unifiedSchedulerButton:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
    text-decoration: none;
    color: white;
}

.ts-unifiedSchedulerButton:active {
    transform: translateY(0);
}

/* Responsive design */
@media (max-width: 1024px) {
    .ts-columns {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .ts-apartmentDetailsContainer {
        padding: 1rem;
    }
}

@media (max-width: 640px) {
    .ts-statusGrid {
        grid-template-columns: 1fr;
    }

    .ts-formGroup input[type="text"],
    .ts-formGroup input[type="number"],
    .ts-formGroup input[type="date"],
    .ts-formGroup input[type="time"],
    .ts-formGroup select,
    .ts-formGroup textarea {
        font-size: 16px;
        padding: 0.875rem;
    }

    .ts-apartmentTitle {
        font-size: 1.5rem;
        padding: 1rem;
    }
}

/* Animation keyframes */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Apply animations */
.ts-detailsGrid,
.ts-planningDetails,
.ts-appointmentDetails {
    animation: fadeIn 0.3s ease-out;
}